import Vue from 'vue'
import VueRouter from 'vue-router'

// navs.js
import navs from './navs'

import NavSearch from '@/components/Nav/NavSearch'
import Search from '@/components/Search/Search'
import SingerList from '@/views/Singer/SingerList'
import SongDetails from '@/views/Common/SongDetails'
import RadioDetails from '@/views/Radio/RadioDetails'
import SingerInfo from '@/views/Singer/SingerInfo'

Vue.use(VueRouter)

//  vue-routerbug
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err);
}

// 
//  + 
const routes = [
    ...navs,
    {
        path: "/search",
        name: "search",
        meta: {
            title: ""
        },
        components: {
            default: Search,
            nav: NavSearch
        }
    },
    // 
    {
        path: "/singerlist/:listId",
        name: "singerlist",
        props: {
            default: true
        },
        components: {
            nav: NavSearch,
            default: SingerList
        }
    },
    // 
    {
        path: "/songdetails/:comName/:detailsId",
        name: "songdetails",
        props: {
            default: true
        },
        components: {
            nav: NavSearch,
            default: SongDetails
        }
    },
    // 
    {
        path: "/radiodetails/:radioId",
        name: "radiodetails",
        props: {
            default: true
        },
        components: {
            nav: NavSearch,
            default: RadioDetails
        }
    },
    // 
    {
        path: "/singerinfo/:singerId",
        props: true,
        name: "singerinfo",
        component: SingerInfo
    }
]

const router = new VueRouter({
    routes
})

export default router