import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    strict: true,
    state: {
        searchTitle: "",
        hash: "", // hash
        currentSongLists: [] // 
    },
    mutations: {
        changeSearchTitle(state, { title }) {
            state.searchTitle = title;
        },
        getSongs(state, { hash }) {
            state.hash = hash;
        },
        commitSongLists(state, { list }) {
            state.currentSongLists = list;
        }
    },
    actions: {},
    modules: {}
})