<template>
  <div class="plist" ref="plist">
    <Cell-com :lists="plists" com-name="plist">
      <template #default="{ count }">
        <span class="playCount">
          <img width="13" src="../../assets/img/icon_music.png" />
          {{ count }}
        </span>
      </template>
    </Cell-com>
  </div>
</template>

<script>
import CellCom from "../Common/Cell";
export default {
  data() {
    return {
      plists: [],
      iPage: 1,
      onOff: false,
    };
  },
  components: {
    CellCom,
  },
  created: async function () {
    // 
    //    
    let { data } = await this.$axios.get("/plist/index?page=1&json=true");
    this.plists = data.plist.list.info.map((item) => {
      return {
        imgurl: item.imgurl,
        specialname: item.specialname,
        specialid: item.specialid,
        playcount: item.playcount,
      };
    });
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFn);
  },
  beforeDestroy(){
    //    
    window.removeEventListener('scroll',this.scrollFn);
  },
  methods: {
    scrollFn() {
      // 
      let conH = this.$refs.plist.offsetHeight;
      console.log(conH);
      // 
      let clientH = document.documentElement.clientHeight;
      // 
      let iTop = document.documentElement.scrollTop;
      console.log(clientH);
      // console.log(this);
      if (iTop + clientH >= conH) {
        if (!this.onOff) {
          this.onOff = true;
          this.iPage++;
          this.$axios
            .get("/plist/index?page=" + this.iPage + "&json=true")
            .then(({data}) => {
              console.log(data);
              let newArr =data.plist.list.info.map((item) => {
                return {
                  imgurl: item.imgurl,
                  specialname: item.specialname,
                  specialid: item.specialid,
                  playcount: item.playcount,
                };
              });
              console.log(newArr);
              this.plists = this.plists.concat(newArr);
              this.$nextTick(function(){  // dom
                this.onOff= false;
              })
            });
        }
      }
    },
  },
};
</script>

<style lang="less">
.playCount {
  position: absolute;
  left: 5rem;
  top: 2.75rem;
  color: #333333;
  font-size: 0.65rem;
  img {
    position: relative;
    top: -0.1rem;
  }
}
</style>